.DeleteButton {
  background: inherit;
  background-color: #fff;
  border: inherit;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  font-size: 1rem;
  text-align: center;
  padding: 0;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.DeleteButton:disabled {
  color: #fff;
}

.DeleteButton:not(:disabled):hover,
.DeleteButton:not(:disabled):focus {
  color: #fff;
  background-color: #f50057;
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
  outline: 0;
}

.Item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: white;
}

.Item.is-selected {
  outline: none;
  box-shadow: -3px 5px 10px -1px #ddd, 3px -5px 10px -1px #ddd;
}

.Item__toolbar {
  background-color: #ddd;
  border-bottom: 1px solid #ddd;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  padding: 7px;
  display: -webkit-flex;
  display: flex;
}

.Item__name {
  display: -webkit-flex;
  display: flex;
  margin-left: 10px;
  overflow: hidden;
  width: 75%;
  vertical-align: text-bottom;
  text-overflow: ellipsis;
  -webkit-align-items: center;
          align-items: center;
}

.Item.is-selected .Item__toolbar {
  color: #fff;
  background-color: #3f51b5;
}

.Item__childrenContainer {
  /* This way children have all the container height available */
  height: 100%;
  padding: 10px;
  overflow: auto;
}

@media (max-width: 799px) {
  .right-side-menu-container {
    width: 100%;
  }

  .main__wrapper {
    width: 0%;
  }
}

@media (min-width: 800px) {
  .right-side-menu-container {
    width: 45%;
  }

  .main__wrapper {
    width: 55%;
  }
}

@media (min-width: 1000px) {
  .right-side-menu-container {
    width: 35%;
  }

  .main__wrapper {
    width: 65%;
  }
}

.center {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.scrollable {
  overflow: auto;
  max-width: 100%;
  max-height: 100%;
}

.app-container {
  min-height: 100vh;
  max-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.main {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  height: 100%;
  overflow: auto;
}

.main__wrapper {
  overflow-y: auto;
}

.region-select {
  border: 1px dashed rgba(0, 0, 0, 0.5);
  outline: rgba(255, 255, 255, 0.5) dashed 1px;
  background-color: rgba(38, 127, 216, 0.33);
  position: absolute;
}

.region-select:hover {
  cursor: move;
}

