.selection-toolbar {
  background: white;
  color: #3f51b5;
  -webkit-flex-direction: row;
          flex-direction: row;
  box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.7);
}

.right-side-menu-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-shadow: -3px 0px 10px -5px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  z-index: 1;
  overflow: hidden;
  -webkit-transition: 0.5s all;
  transition: 0.5s all;
}

.right-side-menu-title {
  background-color: #3f51b5;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.right-side-menu-title span {
  color: white;
  margin: 17px;
  font-size: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-justify-content: center;
          justify-content: center;
  font-weight: 500;
}

.right-side-menu-feed {
  height: 100%;
  -webkit-align-items: center;
  align-items: center;
  padding: 4%;
}

.image-info {
  font-family: Roboto;
  margin: 40px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

